"use client"

import { zodResolver } from "@hookform/resolvers/zod"
import { useForm, useFieldArray } from "react-hook-form"
import * as z from "zod"
import { Button } from "@/components/ui/button"
import {
    Form,
    FormControl,
    FormDescription,
    FormField,
    FormItem,
    FormLabel,
    FormMessage,
} from "@/components/ui/form"
import { Input } from "@/components/ui/input"
import { Textarea } from "@/components/ui/textarea"
import { useData, Service } from "@/lib/data-context"
import { useRouter } from "next/navigation"
import { toast } from "sonner"
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Plus, Trash2 } from "lucide-react"

const formSchema = z.object({
    title: z.string().min(2, {
        message: "Title must be at least 2 characters.",
    }),
    titleAr: z.string().optional(),
    description: z.string().min(10, {
        message: "Description must be at least 10 characters.",
    }),
    descriptionAr: z.string().optional(),
    features: z.array(z.string().min(1, "Feature cannot be empty")).min(1, "At least one feature is required"),
    featuresAr: z.array(z.string().optional()).optional(),
    iconName: z.string().optional(),
    color: z.string().optional(),
    image: z.string().min(1, {
        message: "Image is required.",
    }),
})

interface ServiceFormProps {
    initialData?: Service
    isEditing?: boolean
}

export function ServiceForm({ initialData, isEditing = false }: ServiceFormProps) {
    const { addService, updateService } = useData()
    const router = useRouter()

    const form = useForm<z.infer<typeof formSchema>>({
        resolver: zodResolver(formSchema),
        defaultValues: {
            title: initialData?.title || "",
            titleAr: initialData?.titleAr || "",
            description: initialData?.description || "",
            descriptionAr: initialData?.descriptionAr || "",
            features: initialData?.features || [""],
            featuresAr: initialData?.featuresAr || [""],
            // Default values for hidden fields
            iconName: initialData?.iconName || "Brain",
            color: initialData?.color || "#7B3FEF",
            image: initialData?.image || "",
        },
    })

    const { fields: featureFields, append: appendFeature, remove: removeFeature } = useFieldArray({
        control: form.control,
        name: "features" as never,
    })

    const { fields: featureArFields, append: appendFeatureAr, remove: removeFeatureAr } = useFieldArray({
        control: form.control,
        name: "featuresAr" as never,
    })

    const handleImageUpload = (e: React.ChangeEvent<HTMLInputElement>) => {
        const file = e.target.files?.[0]
        if (file) {
            const imageUrl = URL.createObjectURL(file)
            form.setValue("image", imageUrl)
        }
    }

    function onSubmit(values: z.infer<typeof formSchema>) {
        const cleanValues = {
            ...values,
            features: values.features.filter(f => f.trim() !== ""),
            featuresAr: (values.featuresAr?.filter((f): f is string => !!f && f.trim() !== "") || []),
            href: initialData?.href || `/services/${values.title.toLowerCase().replace(/\s+/g, "-")}`,
            color: values.color || "#7B3FEF",
            iconName: values.iconName || "Brain",
        }

        if (isEditing && initialData) {
            updateService(initialData.id, cleanValues)
            toast.success("Service updated successfully")
        } else {
            addService(cleanValues)
            toast.success("Service created successfully")
        }

        router.push("/dashboard/services")
        router.refresh()
    }

    return (
        <Form {...form}>
            <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-8 max-w-2xl">
                <Tabs defaultValue="english" className="w-full">
                    <TabsList className="grid w-full grid-cols-2">
                        <TabsTrigger value="english">English</TabsTrigger>
                        <TabsTrigger value="arabic">Arabic</TabsTrigger>
                    </TabsList>

                    <TabsContent value="english" className="space-y-6 mt-6">
                        <FormField
                            control={form.control}
                            name="title"
                            render={({ field }) => (
                                <FormItem>
                                    <FormLabel>Title (English)</FormLabel>
                                    <FormControl>
                                        <Input placeholder="Service Title" {...field} />
                                    </FormControl>
                                    <FormMessage />
                                </FormItem>
                            )}
                        />
                        <FormField
                            control={form.control}
                            name="description"
                            render={({ field }) => (
                                <FormItem>
                                    <FormLabel>Description (English)</FormLabel>
                                    <FormControl>
                                        <Textarea placeholder="Service Description" {...field} />
                                    </FormControl>
                                    <FormMessage />
                                </FormItem>
                            )}
                        />

                        <div className="space-y-4">
                            <div className="flex items-center justify-between">
                                <FormLabel>Features (English)</FormLabel>
                                <Button
                                    type="button"
                                    variant="outline"
                                    size="sm"
                                    onClick={() => appendFeature("")}
                                >
                                    <Plus className="w-4 h-4 mr-2" />
                                    Add Feature
                                </Button>
                            </div>
                            {featureFields.map((field, index) => (
                                <div key={field.id} className="flex gap-2">
                                    <FormField
                                        control={form.control}
                                        name={`features.${index}`}
                                        render={({ field }) => (
                                            <FormItem className="flex-1">
                                                <FormControl>
                                                    <Input placeholder="Feature" {...field} />
                                                </FormControl>
                                                <FormMessage />
                                            </FormItem>
                                        )}
                                    />
                                    <Button
                                        type="button"
                                        variant="ghost"
                                        size="icon"
                                        onClick={() => removeFeature(index)}
                                        disabled={featureFields.length === 1}
                                    >
                                        <Trash2 className="w-4 h-4 text-destructive" />
                                    </Button>
                                </div>
                            ))}
                        </div>
                    </TabsContent>

                    <TabsContent value="arabic" className="space-y-6 mt-6" dir="rtl">
                        <FormField
                            control={form.control}
                            name="titleAr"
                            render={({ field }) => (
                                <FormItem>
                                    <FormLabel>Title (Arabic)</FormLabel>
                                    <FormControl>
                                        <Input placeholder="عنوان الخدمة" {...field} className="text-right" />
                                    </FormControl>
                                    <FormMessage />
                                </FormItem>
                            )}
                        />
                        <FormField
                            control={form.control}
                            name="descriptionAr"
                            render={({ field }) => (
                                <FormItem>
                                    <FormLabel>Description (Arabic)</FormLabel>
                                    <FormControl>
                                        <Textarea placeholder="وصف الخدمة" {...field} className="text-right" />
                                    </FormControl>
                                    <FormMessage />
                                </FormItem>
                            )}
                        />

                        <div className="space-y-4">
                            <div className="flex items-center justify-between">
                                <FormLabel>Features (Arabic)</FormLabel>
                                <Button
                                    type="button"
                                    variant="outline"
                                    size="sm"
                                    onClick={() => appendFeatureAr("")}
                                >
                                    <Plus className="w-4 h-4 mr-2" />
                                    Add Feature
                                </Button>
                            </div>
                            {featureArFields.map((field, index) => (
                                <div key={field.id} className="flex gap-2">
                                    <FormField
                                        control={form.control}
                                        name={`featuresAr.${index}`}
                                        render={({ field }) => (
                                            <FormItem className="flex-1">
                                                <FormControl>
                                                    <Input placeholder="ميزة" {...field} className="text-right" />
                                                </FormControl>
                                                <FormMessage />
                                            </FormItem>
                                        )}
                                    />
                                    <Button
                                        type="button"
                                        variant="ghost"
                                        size="icon"
                                        onClick={() => removeFeatureAr(index)}
                                    >
                                        <Trash2 className="w-4 h-4 text-destructive" />
                                    </Button>
                                </div>
                            ))}
                        </div>
                    </TabsContent>
                </Tabs>

                {/* Hidden fields for icon and color */}
                <input type="hidden" {...form.register("iconName")} />
                <input type="hidden" {...form.register("color")} />

                <FormField
                    control={form.control}
                    name="image"
                    render={({ field }) => (
                        <FormItem>
                            <FormLabel>Service Image</FormLabel>
                            <FormControl>
                                <div className="space-y-4">
                                    <Input
                                        type="file"
                                        accept="image/*"
                                        onChange={handleImageUpload}
                                    />
                                    {field.value && (
                                        <div className="relative w-full h-48 rounded-lg overflow-hidden border">
                                            <img
                                                src={field.value}
                                                alt="Preview"
                                                className="object-cover w-full h-full"
                                            />
                                        </div>
                                    )}
                                    {/* Hidden input to store the URL */}
                                    <input type="hidden" {...field} />
                                </div>
                            </FormControl>
                            <FormDescription>
                                Upload a cover image for the service.
                            </FormDescription>
                            <FormMessage />
                        </FormItem>
                    )}
                />

                <Button type="submit">{isEditing ? "Update Service" : "Create Service"}</Button>
            </form>
        </Form>
    )
}
